<template>
  <v-tab :items="items">
    <div slot="common">
      <div class="layui-form-item">
        <label class="layui-form-label"></label>
        <div class="layui-input-block">
          <input type="text" name="title" required lay-verify="required" placeholder="" autocomplete="off"
            class="layui-input">
        </div>
      </div>
    </div>
    <div slot="menus">
      <div class="layui-form-item layui-form-text">
        <v-select v-model="selectValue" :options="menuOptions" :hasNull="true" @change="changeSelect"></v-select>
      </div>
      <div class="layui-form-item layui-form-text">
        <input type="text" v-model="selectMenu.menuName" placeholder="" class="layui-input">
      </div>
      <div class="layui-form-item layui-form-text">
        <input type="text" v-model="selectMenu.menuUrl" placeholder="" class="layui-input">
      </div>
      <div class="layui-form-item layui-form-text">
        <input type="number" v-model="selectMenu.sort" placeholder="" class="layui-input">
      </div>
      <div class="layui-form-item layui-form-text">
        
        <v-checkbox v-model="selectMenu.indexHide" text="" />
        <v-checkbox v-model="selectMenu.navHide" text="" />
      </div>
      <div class="layui-form-item layui-form-text" v-if="selectValue">
        <v-button @click="saveSetting"></v-button>
        <v-button @click="removeCustomMenu" v-if="selectMenu.custom"></v-button>
      </div>
      <div class="layui-form-item layui-form-text" v-else>
        <v-button @click="addCustomMenu"></v-button>
      </div>
    </div>
    <div slot="fanyi">
      <fieldset class="layui-elem-field">
        <legend></legend>
        <div class="layui-field-box">
          <div class="layui-form-item layui-form-text">
            <input type="text" v-model="fanyiConfig.baiduAppId" placeholder="APPID" class="layui-input">
          </div>
          <div class="layui-form-item layui-form-text">
            <input type="text" v-model="fanyiConfig.baiduAppKey" placeholder="" class="layui-input">
          </div>
        </div>
      </fieldset>
      <fieldset class="layui-elem-field">
        <legend></legend>
        <div class="layui-field-box">
          <div class="layui-form-item layui-form-text">
            <input type="text" v-model="fanyiConfig.youdaoAppId" placeholder="ID" class="layui-input">
          </div>
          <div class="layui-form-item layui-form-text">
            <input type="text" v-model="fanyiConfig.youdaoAppKey" placeholder="" class="layui-input">
          </div>
        </div>
      </fieldset>
      <div class="layui-form-item layui-form-text">
        <v-button @click="saveFanyiConfig"></v-button>
      </div>
    </div>
  </v-tab>
</template>

<script>
export default {
  meta: {
    navHide: true,
    menuName: '',
    sort: 1
  },
  data() {
    return {
      items: [
        {
          Title: '',
          Name: 'menus'
        },
        {
          Title: '',
          Name: 'fanyi'
        }
      ],
      setting: localStorage.CustomSetting || {},
      menus: [],
      selectValue: null,
      menuOptions: [],
      selectMenu: {},
      fanyiConfig: {
        // baiduAppId: '',
        // baiduAppKey: '',
        // youdaoAppId: '',
        // youdaoAppKey: '',
      }
    }
  },
  created() {
    this.fanyiConfig = JSON.parse(localStorage.getItem('fanyi_config') || 'null') || this.fanyiConfig
    this.menus = window.siteMenuItems
    this.menuOptions = window.siteMenuItems.map(e => {
      return {
        Text: e.menuName,
        Value: e.menuUrl
      }
    })
  },
  methods: {
    changeSelect(item) {
      this.selectMenu = this.menus.find(e => e.menuUrl === item) || {}
    },
    checkForm() {
      if (!this.selectMenu.menuName || !this.selectMenu.menuUrl) {
        window.layui.layer.msg('')
        return false
      }
      return true
    },
    saveSetting() {
      if (!this.checkForm()) return
      localStorage.setItem(
        this.selectMenu.menuUrl,
        JSON.stringify(Object.assign({ sort: 0 }, this.selectMenu))
      )
      this.reload()
    },
    addCustomMenu() {
      if (!this.checkForm()) return
      var customMenus = JSON.parse(localStorage.InsertMenus || '[]')
      if (
        customMenus.find(
          e =>
            e.menuName === this.selectMenu.menuName ||
            e.menuUrl === this.selectMenu.menuUrl
        )
      ) {
        return layui.msg('Url')
      }
      customMenus.push(
        Object.assign({ custom: true, sort: 0 }, this.selectMenu)
      )
      localStorage.setItem('InsertMenus', JSON.stringify(customMenus))
      this.reload()
    },
    removeCustomMenu() {
      var customMenus = JSON.parse(localStorage.InsertMenus || '[]')
      customMenus = customMenus.filter(
        e =>
          e.menuName !== this.selectMenu.menuName &&
          e.menuUrl !== this.selectMenu.menuUrl
      )
      localStorage.setItem('InsertMenus', JSON.stringify(customMenus))
      localStorage.removeItem(this.selectMenu.menuUrl)
      this.reload()
    },
    reload() {
      location.reload()
    },
    saveFanyiConfig() {
      // console.log(this.fanyiConfig)
      //
      localStorage.setItem('fanyi_config', JSON.stringify(this.fanyiConfig, (key, value) => {
        if (value !== null && value !== undefined && value !== "") {
          return value;
        }
      }))
    }
  }
}
</script>

<style></style>



